@use 'base/variable' as v;
@use 'base/mixins' as m;

.header{
    padding: 5rem 0;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include m.tablet {
        min-height: 70rem;
    }
    
    &::before{
        content: '';
        background-color:v.$negro;
        position: absolute;
        top:0;
        right: 0;
        left: 0;
        bottom: 0;
        opacity: .4;
    }

    .contenido-header{
        position: relative;

        h1{
            text-align: center;
            color: v.$blanco;
            text-transform: uppercase;
            margin-top: 5rem;

            @include m.tablet {
                margin-top: 10rem;
            }
        }
    }
}

