@use 'base/mixins' as m;
@use 'base/variable' as v;

.footer{
    background-color: v.$primario;
    .contenido-footer{
        padding: 5rem;

        @include m.tablet {
            @include m.grid(3,2rem)
        }

    }

    .copyright{
        text-align: center;
        margin:0;
        padding: 2rem;
        color: v.$blanco;
    }
}

.footer-box{
    font-family: v.$fuente-cursiva;
    text-align: center;
    margin-bottom: v.$separacion;

    @include m.tablet {
        margin-bottom:0;
    }

    h3{
        font-family: v.$fuente-cursiva;
        margin: 0 0 1rem 0;
        font-size: 2.4rem;
        color:v.$secundario;
        
    }

    p,a{
        font-size: 2rem;
        margin: 0;
    }
}