@use 'base/variable' as v;
@use 'base/mixins' as m;

.proceso-cafe{
    margin-bottom: v.$separacion;
    @include m.tablet {
        display: flex;
        gap: v.$separacion;
        align-items: center;
    }

    &:last-of-type{
        margin-bottom: 0;
    }

    .imagen,.texto{
        flex: 1;
    }

    &:nth-child(even){
        flex-direction: row-reverse;
    }
    .texto{

        margin-top: 1rem;
        h3{
            margin: 0;
            text-align: left;
            color: v.$primario;

            &::after{
                content: '';
                display: block;
                width: 25%;
                height: 5px;
                background-color: v.$secundario;
            }
        }
    }
}